.lbl{
  display: grid;
  justify-content: center; 
  grid-template-columns: auto  30% ; 
   grid-column-gap: 15px;
 width: 100%;
 height: 50%;
}

.btn{
  display: grid;
  justify-content: center;
  grid-template-columns: 80px 80px;
  grid-column-gap: 10px;
  margin-left: 255px;
  width: auto;
 height: auto;
}
h1{
    color: rgb(0, 0, 0);
    text-align: center;
    text-decoration: underline;
    width: auto;
    height: auto;
   
}

.Msg{
  color: red;
  
}

@media screen and (max-width:300px){
span.lbl{
  display: block;
  float: none;
}  
}